import React, { useEffect, useState } from 'react';
import axios from '../../utils/axios';
import styles from './myReviews.module.css';
import { AiFillHeart } from "react-icons/ai";
import { useHistory } from 'react-router-dom';
import PulseLoader from "react-spinners/PulseLoader";
import 'react-toastify/dist/ReactToastify.css';


function MyReviews({userId,endPoint}) {
    const [reviews, setReviews] = useState([]);
    const [loading, setLoading] = useState(true);
    const history = useHistory();
    const fetchData = async () => {
        const axiosInstance = axios.createInstance();
        const result = await axiosInstance.get(`${endPoint}?id=${userId}`).catch((err)=>{

        });
        if(result.data){
            setReviews(result.data);
        }else{
            //error
        }
        setLoading(false);
    }

    const toggleShowMore = (index) =>{
        let newArr = [...reviews];
        newArr[index].showMore = !newArr[index].showMore;
        setReviews(newArr);
    }

    const goToShow = (id) => {
        if(id){
            history.push(`/show/${id}`);
        }
    }

    const wordLimit = 700;
    const createMarkup = (review) => {
        return {
            __html: review?.showMore? review.review : review.review.length<=wordLimit ? review.review : review.review.substring(0,wordLimit) + " ..."
        };
    }

    useEffect(() => {
        fetchData();
        return () => {
            
        }
    }, [userId])
    return (
        <div className={styles.reviews}>
            {loading && <div className = {styles.loader}><PulseLoader color="#ffff"/></div>}
            {
                    reviews?.map((review, index)=> {
                        return <div className={`${styles.review} ${styles.make_flex}`} key={index} onClick={()=>{goToShow(review.show_id)}}>
                            <div >
                                <img className={`${styles.avatar} ${styles.neumorphism}`} src="data:image/png;base64,"></img>
                            </div>
                            <div className={styles.review_detail}>
                                <div className={styles.review_text} dangerouslySetInnerHTML={createMarkup(review)}/>
                                {review.review.length<=wordLimit? "" : review?.showMore? <p className={styles.showMore} onClick={()=>{toggleShowMore(index)}}>Show Less</p> : <p className={styles.showMore} onClick={()=>{toggleShowMore(index)}}>Show More</p>}
                                <div className={styles.make_flex}>
                                    <AiFillHeart id={review.id} className={`${styles.heart} ${review.current_user ? styles.red_heart : ""}`} />  
                                    <div className={styles.likes} id={`like_${review.id}`}>{review.likes}</div>      
                                </div>
                            </div>               
                        </div>
                    })
                }
        </div>
    )
}

export default MyReviews
